import React from 'react';
//import connect from '@vkontakte/vkui-connect';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel'
import Root from '@vkontakte/vkui/dist/components/Root/Root'
import View from '@vkontakte/vkui/dist/components/View/View'
import '@vkontakte/vkui/dist/vkui.css';
import queryString from 'query-string';
import Header from './common/Header'
import SubscribePanel from './common/SubscribePanel'
import connect from '@vkontakte/vkui-connect'
import StatisticPanel from "./common/StatisticPanel";
import Alert from "@vkontakte/vkui/dist/components/Alert/Alert";


var sprintf = require('sprintf-js').sprintf;

const switchSubscribe = 'https://api.sovetromantica.com/api/v1/notify/vkapp/switch/%s/%s';
const reqStatisticUrl = 'https://api.sovetromantica.com/api/v1/notify/vkapp/statistic/%s';
const reqStatisticTitleUrl = 'https://api.sovetromantica.com/api/v1/notify/vkapp/statistic/title/%s';
const reqSubscribeList = 'https://api.sovetromantica.com/api/v1/notify/vkapp/get';
const reqSubscribeOldList = 'https://api.sovetromantica.com/api/v1/notify/vkapp/get/old';
const reqSubscribeClear = 'https://api.sovetromantica.com/api/v1/notify/vkapp/clear';

class App extends React.Component {
    constructor(props) {
        super(props);
        let params = queryString.parse(window.location.search);
        let data = new FormData();
        for (let par in params) {
            data.append(par, params[par]);
        }
        this.state = {
            mode: 'subscribe',
            rights: params.vk_viewer_group_role,
            userId: params.vk_user_id,
            clientType: params.vk_platform,
            urlData: data,
            popout: null,
        };
        this.selectScreen = this.selectScreen.bind(this);
        this.resizeVkIframe = this.resizeVkIframe.bind(this);
        this.switchSubscribeStatus = this.switchSubscribeStatus.bind(this);
        this.getSubscribeList = this.getSubscribeList.bind(this);
        this.clearSubscriptions = this.clearSubscriptions.bind(this);
        this.clearSubscriptionsLaunch = this.clearSubscriptionsLaunch.bind(this);
        this.closePopout = this.closePopout.bind(this);
        this.getStatistic = this.getStatistic.bind(this);

        this.height = 0;
        if (this.state.rights === 'editor' || this.state.rights === 'admin') {
            let hash = queryString.parse(window.location.hash);
            if (hash.mode === 'subscribe' || hash.mode === 'statistic_full' || hash.mode === 'statistic_cur') {
                this.state.mode = hash.mode;
            }

        }
    }


    selectScreen(e) {
        this.setState({mode: e.currentTarget.dataset.mode});
        if (this.state.rights === 'editor' || this.state.rights === 'admin') {
            let hash = queryString.parse(window.location.hash);
            hash.mode = e.currentTarget.dataset.mode;
            connect.send("VKWebAppSetLocation", {"location": queryString.stringify(hash)});
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.resizeVkIframe();
    }

    componentDidMount() {
        this.resizeVkIframe();
    }


    resizeVkIframe() {
        if (this.state.clientType === 'desktop_web') {
            let height = document.getElementById(this.state.mode + "_size").scrollHeight + 110;
            if (this.height !== height) {
                this.height = height;
                connect.send('VKWebAppResizeWindow', {"width": 730, "height": height});
            }

        }
    }

    async getSubscribeList(type = 'current') {
        let resp = await fetch((type === 'current') ? reqSubscribeList : reqSubscribeOldList, {
            method: 'POST',
            body: this.state.urlData
        });
        let json = await resp.json();
        for (let el in json) {
            json[el].showed = true;
        }
        return json;
        //this.resizeVkIframe();
    }

    async getStatistic(titleId = 0) {
        if(titleId===0){
            return undefined;
        }
        let resp = await fetch(sprintf(reqStatisticTitleUrl, titleId), {
            method: 'POST',
            body: this.state.urlData
        });
        return await resp.json();
    }

    async clearSubscriptions() {
        await fetch(reqSubscribeClear, {method: 'POST', body: this.state.urlData});
    }

    clearSubscriptionsLaunch(returnFnc) {
        this.setState({
            popout:
                <Alert actions={[
                    {
                        title: "Удалить",
                        autoclose: true,
                        style: "destructive",
                        action:returnFnc
                    },
                    {
                        title: "Отмена",
                        autoclose: true,
                        style: "cancel"
                    }]}
                onClose={this.closePopout}>
                    <h2>Подтвердите действие</h2>
                    <p>Вы уверены, что хотите удалить все уведомления? <br/>Будут удалены все текущие, а также старые подписки. <br/>Вы перестаните получать уведомления о новых сериях.</p>
                </Alert>
        })
    }

    closePopout(e){
        console.log(e);
        this.setState({popout: null});
    }

    async switchSubscribeStatus(titleId, titleType) {
        if (titleId !== 0 && titleId !== undefined && titleType !== undefined) {
            let resp = await fetch(sprintf(switchSubscribe, titleId, titleType), {
                method: 'POST',
                body: this.state.urlData
            });
            let json = await resp.json();
            return json;
        } else {
            console.log("switch parameters are wrong")
        }
    }

    async loadStatistic(minimumId) {
        if (minimumId == undefined) {
            minimumId = 0;
        }
        let resp = await fetch(sprintf(reqStatisticUrl, minimumId), {method: 'POST', body: this.state.urlData});
        let json = await resp.json();
        return json;
    }

    render() {
        return (

            <Root activeView="subTitle">
                <View id="subTitle" activePanel={this.state.mode} popout={this.state.popout}>

                    <Panel id="subscribe">

                        <Header rights={this.state.rights} mode="subscribe" select={this.selectScreen}
                                client={this.state.clientType}/>

                        <SubscribePanel switch={this.switchSubscribeStatus} resize={this.resizeVkIframe}
                                        subscribe={this.getSubscribeList} client={this.state.clientType}
                                        rights={this.state.rights} clearDisplay={this.clearSubscriptionsLaunch} clear={this.clearSubscriptions}/>

                    </Panel>
                    <Panel id="statistic_full">
                        <Header rights={this.state.rights} mode="statistic_full" select={this.selectScreen}
                                client={this.state.clientType}/>
                        <StatisticPanel minimumId="0" loader={this.loadStatistic.bind(this)}
                                        client={this.state.clientType} mode={this.state.mode}
                                        resize={this.resizeVkIframe} statistic={this.getStatistic}/>
                    </Panel>
                    <Panel id="statistic_cur">
                        <Header rights={this.state.rights} mode="statistic_cur" select={this.selectScreen}
                                client={this.state.clientType}/>
                        <StatisticPanel minimumId="885" loader={this.loadStatistic.bind(this)}
                                        client={this.state.clientType} mode={this.state.mode}
                                        resize={this.resizeVkIframe} statistic={this.getStatistic}/>
                    </Panel>

                </View>
            </Root>


        );
    }
}

export default App;
