import React from 'react';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress'
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter'
import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';
import Chart from 'react-apexcharts';
import Spinner from '@vkontakte/vkui/dist/components/Spinner/Spinner';
var dateFormat = require('dateformat');

class StatisticBlock extends React.Component {


    constructor(props) {
        super(props);
        this.state = {
            opened: false,
            statDataAdded: undefined,
            statDataRemoved: undefined,
            statDataDate: undefined
        };
        this.click = this.click.bind(this);
    }

    async click(e) {
        this.setState({opened: !this.state.opened})
        if(!this.state.opened){
            this.setState({statDataAdded:undefined,statDataRemoved:undefined, statDataDate:undefined})
            let json = await this.props.statistic(this.props.data.anime_id);
            let added = [];
            let removed = [];
            let axis = [];
            json.forEach(function(item, i, arr) {
                added.push(item.added);
                removed.push(item.removed);
                axis.push(dateFormat(Date.parse(item.date),"dd.mm"));
            });
            this.setState({statDataAdded:added,statDataRemoved:removed, statDataDate:axis});
        }

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.props.resize();
    }

    render() {
        return (
            <div>
                <div className="subscribeRow" onClick={this.click} key={"sub_" + this.props.data.anime_id}>
                    <div className="subscribeIconExtender">
                        {this.state.opened ? <Icon24Dropdown fill="var(--accent)"/> :
                            <Icon24Chevron fill="var(--accent)"/>}
                    </div>
                    <div className="subscribeRowFullContent">
                        <div className="subscribeRowContent">
                            <div
                                className="subscribeRowTitleNameContainer">{this.props.data.anime_name}</div>
                            <div className="statisticRowProgressContainer">
                                <Progress value={100 * this.props.data.total / this.props.maxVotes}/>
                            </div>
                        </div>

                    </div>
                    <div>
                        <Counter type="primary">{this.props.data.sub}</Counter>
                        <Counter>{this.props.data.dub}</Counter>
                    </div>
                </div>
                <div className="fullTitleInformation" style={{"display": (this.state.opened) ? "flex" : "none","min-height":"10px"}}>
                    {this.state.statDataAdded===undefined && this.state.statDataRemoved===undefined && <Spinner size="large" className="fullTitleInformationSpinner"/>}
                    {this.state.statDataAdded!==undefined && this.state.statDataRemoved!==undefined && <Chart type="line" width="100%" height="200px" series={[
                            {name:"Подписались", data:this.state.statDataAdded},
                            {name:"Отписались", data:this.state.statDataRemoved},
                        ]}
                        options={{
                            chart:{
                                background:"var(--background_light)",
                                foreColor:"var(--accent)",
                                defaultLocale:"en",

                                toolbar:{
                                    show:false
                                },
                                animations:{
                                    enabled: false
                                }
                            },
                            dataLabels: {
                                enabled: true,
                            },
                            tooltip:{
                                enabled:false,
                                shared: false,
                                marker:{
                                    show:false
                                }
                            },
                            labels:this.state.statDataDate,
                            stroke:{
                                curve:'smooth',
                                width:'2'
                            },
                            xaxis:{
                                //type:'datetime',
                                labels:{
                                    showDuplicates: true
                                },
                                tooltip:{
                                    enabled: false
                                }
                            },
                            yaxis:{
                                tooltip:{
                                    enabled:false
                                }
                            }
                        }}
                    /> }
                </div>
            </div>

        );

    }
}

export default StatisticBlock