import React from 'react';
import PanelSpinner from "@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner";
import Search from "@vkontakte/vkui/dist/components/Search/Search";
import FixedLayout from "@vkontakte/vkui/dist/components/FixedLayout/FixedLayout";
import SubscribeBlock from "./SubscribeBlock";
import Tabs from "@vkontakte/vkui/dist/components/Tabs/Tabs";
import TabsItem from "@vkontakte/vkui/dist/components/TabsItem/TabsItem";
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from "@vkontakte/vkui/dist/components/Button/Button";


class SubscribePanel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            subscribeTitles: [],
            loading: true,
            mode: "current",
        };
        this.searchUpdate = this.searchUpdate.bind(this);
        this.switch = this.switch.bind(this);
        this.returnClear = this.returnClear.bind(this);
        this.sendClearRequest = this.sendClearRequest.bind(this);
    }

    async componentDidMount() {
        this.setState({subscribeTitles: await this.props.subscribe(), loading: false});
        //this.resizeVkIframe();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        setTimeout(this.props.resize(), 100);
    }

    async returnClear(){
        await this.props.clear();
        this.setState({subscribeTitles: [], loading: true})
        this.setState({subscribeTitles: await this.props.subscribe(this.state.mode), loading: false});
    }

    searchUpdate(e) {
        for (let el in this.state.subscribeTitles) {
            if (this.state.subscribeTitles[el].anime_name.toLowerCase().indexOf(e.toLowerCase()) !== -1 || this.state.subscribeTitles[el].anime_name_rus.toLowerCase().indexOf(e.toLowerCase()) !== -1) {
                this.state.subscribeTitles[el].showed = true;
            } else {
                this.state.subscribeTitles[el].showed = false;
            }
            this.setState({subscribeTitles: this.state.subscribeTitles});
        }
    }

    async switch(e) {
        this.setState({loading: true, mode: e.currentTarget.dataset.mode, subscribeTitles: []});
        let titles = await this.props.subscribe(e.currentTarget.dataset.mode);
        this.setState({subscribeTitles: titles, loading: false});
    }

    sendClearRequest(){
        this.props.clearDisplay(this.returnClear);
    }

    render() {
        return (
            <div id="subscribe_size"
                 style={{
                     "paddingTop": (this.props.client === 'desktop_web') ? "50px" : "60px",
                     "paddingBottom": (this.props.client === 'desktop_web') ? "0px" : "60px"
                 }}>
                {this.props.client === 'desktop_web' ? <FixedLayout vertical="top">
                    <Tabs>
                        <TabsItem after={<Icon24List/>} selected={this.state.mode === "current"} data-mode="current"
                                  onClick={this.switch}>Основной</TabsItem>
                        <TabsItem after={<Icon24Recent/>} selected={this.state.mode === "old"} data-mode="old"
                                  onClick={this.switch}>Устаревший</TabsItem>
                    </Tabs>
                </FixedLayout> : null}

                {this.props.client === 'desktop_web' ?
                    <Search className="Search--web" theme="default" onChange={this.searchUpdate}/> :
                    <FixedLayout vertical="top"><Search theme="default" onChange={this.searchUpdate}/></FixedLayout>}
                {this.state.loading && <PanelSpinner size="large"/>}
                {this.state.subscribeTitles.length > 0 && this.state.subscribeTitles.map(item => (
                    (item.showed) ?
                        <SubscribeBlock anime={item} switch={this.props.switch}
                                        key={"sub_" + item.anime_id} resize={this.props.resize}
                                        mode={this.state.mode}/> :
                        null

                ))}
                {(this.state.subscribeTitles.length === 0 && !this.state.loading) && <Div style={{
                    "color": "var(--accent)",
                    "justifyContent": "space-around",
                    "width": "100%",
                    "display": "flex"
                }}>Отсутствуют аниме</Div>}
                {!this.state.loading &&
                <Div style={{"color":"var(--accent)","justifyContent":"space-around", "display":"flex", "marginTop":"15px"}}>
                    <Button stretched style={{"height":"40px"}} onClick={this.sendClearRequest}>Отписаться от всех рассылок</Button>
                </Div>
                }
                {this.props.client !== 'desktop_web' ?
                    <FixedLayout vertical="bottom">
                        <Tabs>
                            <TabsItem after={<Icon24List/>} selected={this.state.mode === "current"} data-mode="current"
                                      onClick={this.switch}>Основной</TabsItem>
                            <TabsItem after={<Icon24Recent/>} selected={this.state.mode === "old"} data-mode="old"
                                      onClick={this.switch}>Устаревший</TabsItem>
                        </Tabs>
                    </FixedLayout>
                    : null}
            </div>
        );
    }
}

export default SubscribePanel;