import React from 'react';
import PanelSpinner from '@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner'
import StatisticBlock from "./StatisticBlock";
class StatisticPanel extends React.Component {


    constructor(props) {
        super(props);
        this.state= {
            minimumId: this.props.minimumId,
            statData: undefined,
            maxVotes: 0,
        };
    }

    async componentDidMount() {
        let json=await this.props.loader(this.state.minimumId);
        if (Array.isArray(json)){
            json.sort(function(a,b){return b.total-a.total;});
            this.setState({statData:json, maxVotes:json[0].total});
        } else {

        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        setTimeout(this.props.resize,300);
    }

    render() {
        return (

            <div id={this.props.mode+"_size"} key={this.props.mode+"_size"}>
                {this.state.statData==undefined && <PanelSpinner size="large"/>}
                {this.state.statData!=undefined && this.state.statData.map((item)=>(
                    <StatisticBlock key={"sb_"+item.anime_id} data={item} maxVotes={this.state.maxVotes} resize={this.props.resize} statistic={this.props.statistic}/>
                ))}
            </div>

        );

    }
}

export default StatisticPanel